"use client"; // Add this directive at the top

// src/app/LandingPage.tsx
import React, { useState } from "react";

export default function AuthTabs() {
  const [activeTab, setActiveTab] = useState<"signup" | "login">("signup");

  return (
    <div className="flex min-h-screen items-center justify-center bg-gray-100 p-6">
      <div className="w-full max-w-lg rounded-lg bg-white p-6 shadow-md">
        {/* Tabs Header */}
        <div className="mb-6 flex justify-center gap-4">
          <button
            onClick={() => setActiveTab("signup")}
            className={`px-6 py-2 text-lg font-medium rounded-lg ${
              activeTab === "signup"
                ? "bg-green-600 text-white"
                : "bg-gray-200 text-gray-800 hover:bg-gray-300"
            }`}
          >
            Sign Up
          </button>
          <button
            onClick={() => setActiveTab("login")}
            className={`px-6 py-2 text-lg font-medium rounded-lg ${
              activeTab === "login"
                ? "bg-blue-600 text-white"
                : "bg-gray-200 text-gray-800 hover:bg-gray-300"
            }`}
          >
            Login
          </button>
        </div>

        {/* Tab Content */}
        {activeTab === "signup" ? <SignUpSection /> : <LoginSection />}
      </div>
    </div>
  );
}

/* Sign Up Section */
function SignUpSection() {
  const [formData, setFormData] = useState({
    username: "",
    email: "",
    password: "",
    confirm_password: "",
  });
  const [error, setError] = useState("");

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData((prev) => ({ ...prev, [e.target.name]: e.target.value }));
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    setError("");

    if (formData.password !== formData.confirm_password) {
      setError("Passwords do not match.");
      return;
    }

    // Simulate form submission (no backend)
    alert("Sign-up form submitted successfully!");
    setFormData({
      username: "",
      email: "",
      password: "",
      confirm_password: "",
    });
  };

  return (
    <div>
      <h2 className="mb-6 text-center text-2xl font-bold text-gray-800">
        Sign Up
      </h2>
      {error && (
        <div className="mb-4 rounded bg-red-100 p-3 text-red-700">{error}</div>
      )}
      <form onSubmit={handleSubmit} className="space-y-4">
        <div>
          <label className="block text-sm font-medium text-gray-700">
            Username
          </label>
          <input
            type="text"
            name="username"
            value={formData.username}
            onChange={handleChange}
            required
            className="mt-1 w-full rounded border border-gray-300 p-2 text-gray-800 focus:ring-2 focus:ring-green-600"
          />
        </div>
        <div>
          <label className="block text-sm font-medium text-gray-700">
            Email
          </label>
          <input
            type="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
            required
            className="mt-1 w-full rounded border border-gray-300 p-2 text-gray-800 focus:ring-2 focus:ring-green-600"
          />
        </div>
        <div>
          <label className="block text-sm font-medium text-gray-700">
            Password
          </label>
          <input
            type="password"
            name="password"
            value={formData.password}
            onChange={handleChange}
            required
            className="mt-1 w-full rounded border border-gray-300 p-2 text-gray-800 focus:ring-2 focus:ring-green-600"
          />
        </div>
        <div>
          <label className="block text-sm font-medium text-gray-700">
            Confirm Password
          </label>
          <input
            type="password"
            name="confirm_password"
            value={formData.confirm_password}
            onChange={handleChange}
            required
            className="mt-1 w-full rounded border border-gray-300 p-2 text-gray-800 focus:ring-2 focus:ring-green-600"
          />
        </div>
        <button
          type="submit"
          className="w-full rounded-lg bg-green-600 py-2 text-lg font-medium text-white hover:bg-green-700"
        >
          Sign Up
        </button>
      </form>
    </div>
  );
}

/* Login Section */
function LoginSection() {
  const [formData, setFormData] = useState({ email: "", password: "" });
  const [error, setError] = useState("");

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData((prev) => ({ ...prev, [e.target.name]: e.target.value }));
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    setError("");

    // Simulate form submission (no backend)
    alert("Login form submitted successfully!");
  };

  return (
    <div>
      <h2 className="mb-6 text-center text-2xl font-bold text-gray-800">
        Login
      </h2>
      {error && (
        <div className="mb-4 rounded bg-red-100 p-3 text-red-700">{error}</div>
      )}
      <form onSubmit={handleSubmit} className="space-y-4">
        <div>
          <label className="block text-sm font-medium text-gray-700">
            Email
          </label>
          <input
            type="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
            required
            className="mt-1 w-full rounded border border-gray-300 p-2 text-gray-800 focus:ring-2 focus:ring-blue-600"
          />
        </div>
        <div>
          <label className="block text-sm font-medium text-gray-700">
            Password
          </label>
          <input
            type="password"
            name="password"
            value={formData.password}
            onChange={handleChange}
            required
            className="mt-1 w-full rounded border border-gray-300 p-2 text-gray-800 focus:ring-2 focus:ring-blue-600"
          />
        </div>
        <button
          type="submit"
          className="w-full rounded-lg bg-blue-600 py-2 text-lg font-medium text-white hover:bg-blue-700"
        >
          Login
        </button>
      </form>
    </div>
  );
}
